@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 88px 0 20px;
    }
    
    .mkdf-comments-title {
        margin-bottom: 43px;
    }

    .mkdf-comment {

        &.mkdf-no-avatar {

            .mkdf-comment-text {
                padding: 0;
            }
        }
    }

    .mkdf-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include mkdfRelativeHolderLayout();
            margin: 30px 0 0;
            padding: 6px 0 0;

            .mkdf-trackback-comment,
            .mkdf-pingback-comment{
                .mkdf-comment-info {
                    display: block;
                }

                .mkdf-comment-date {
                    margin: 0 10px 0 0;
                }
            }
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 20px 10%;
        }
    }

    .mkdf-comment-image {
        position: relative;
        top: 12px;
        left: 0;
        width: 158px;
        height: 158px;
        display: block;
        float: left;
        
        @include ipad-landscape {
            width: 90px;
            height: 90px;
        }
    
        @include phone-landscape {
            width: 60px;
            height: 60px;
        }

        img {
            display: block;
        }
    }

    .mkdf-comment-text {
        padding: 0 0 0 181px;
        min-height: 126px;
    
        @include ipad-landscape {
            min-height: 110px;
            padding: 0 0 0 123px;
        }
    
        @include phone-landscape {
            padding: 0 0 0 80px;
        }

        .mkdf-comment-info {
            display: inline-block;
            margin: 6px 0 3px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            font-size: 16px;
            font-weight: 600;
            text-transform: uppercase;
            color: $default-heading-color;
            font-family: $default-heading-font;
            position: relative;
            display: inline-block;
            margin-top: 6px;
            margin-right: 10px;

            &:last-of-type {
                margin-right: 0;
            }

            &:after {
                content: url("../img/arrow.svg");
                vertical-align: top;
                margin-left: 9px;
                display: inline-block;
            }

            &:hover {
                color: $first-main-color;
            }
        }

        .mkdf-comment-name {
            margin: 0;
        }

        .mkdf-comment-date {
            margin: 0 0 0 15px;
            font-size: 18px;
            display: inline-block;
            color: $default-text-color;
            font-family: $additional-font;
            font-style: italic;
            font-weight: 400;

            &:before  {
                content: "\e023";
                font-size: 15px;
                font-family: "eleganticons";
                margin-right: 6px;
                color: inherit;
                font-style: normal;
            }

            @include phone-landscape {
                margin: 0 0 0 6px;
            }
        }

        .mkdf-text-holder p {
            margin: 7px 0;
            line-height: 23px;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }

            ~ .comment-reply-link,
            ~ .comment-edit-link {
                display: none;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.mkdf-comment-form {
    @include mkdfRelativeHolderLayout();
    margin: 86px 0 40px;
    clear: both;

    .comment-reply-title {
        margin: 0 0 41px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0 0 10px 30px;
            }
        }
    }
}

@include phone-landscape {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding-left: 0;
            }
        }

        .mkdf-comment-text {

            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-image {
            float: none;
        }

        .mkdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

#respond input[type='text'] {
    width: calc((100% - 56px) / 3);
    margin-right: 28px;
    float: left;

    .mkdf-edge &,
    .mkdf-ms-explorer & {
        margin-right: 27.9px;
    }

    @include phone-landscape {
        width: 100%;
        margin-right: 0;
        float: none;
    }

    &:last-of-type,
    &#url {
        margin-right: 0;
    }
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

div.wpcf7-validation-errors {
    border-color: $first-main-color;
    margin: 2em 0;
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit']{
    @include mkdfButtonDefaultStyle();
    @include mkdfButtonSize();
    @include mkdfButtonSolidColor();

    cursor: pointer;

    &:hover {
        @include mkdfButtonSolidHoverColor();
    }
}

#respond input[type="text"]:last-of-type:not(:last-child) {
    margin-bottom: 0;
}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea {
    display: block;
}

#respond {
    .comment-form-cookies-consent {
        clear: both;
        margin-bottom: 10px;
        position: relative;
        top: -11px;

        label {
            line-height: 26px;

            .mkdf-chrome & {
                position: relative;
                top: -1px;
            }
        }
    }
}

.mkdf-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

.custom-footer-newsletter {
    .wpcf7-form-control.wpcf7-text {
        border-top: none;
        border-right: none;
        max-width: 222px;
        padding-left: 20px;
        color: #fff;

        .mkdf-dark-footer & {
            color: $default-heading-color;
        }

        &::placeholder {
            color: #fff;

            .mkdf-dark-footer & {
                color: $default-text-color;
            }
        }

    }

    .your-name {
        .wpcf7-text {
            margin-bottom: 20px;
        }
    }

    .mkdf-btn.mkdf-btn-solid {
        position: relative;
        background-color: transparent;
        padding: 0;
        color: #fff;
        margin-top: 10px;
        overflow: hidden;

        @include ipad-landscape {
            width: 100%;
            text-align: left;
        }

        .mkdf-dark-footer & {
            color: $default-heading-color;
        }

        .mkdf-btn-text {
            display: inline-block;
            transition: .3s;
            padding-right: 20px;
        }

        &:hover {
            background-color: transparent !important;

            .mkdf-btn-text {
                transform: translateX(18px);
                opacity: .1;
                transition: .3s .2s;
            }

            &:before {
                left: 37%;
                transition: .3s .2s;
            }

            &:after {
                transform: rotate(45deg);
                right: -24px;
                transition: right .3s .2s, transform .3s;
            }
        }
    
        .mkdf-dark-footer & {
            &:before,
            &:after {
                content: url("../img/arrow.svg");
            }
        }

        &:before {
            content: url("../img/arrow_light.svg");
            vertical-align: top;
            margin-left: 9px;
            display: inline-block;
            position: absolute;
            transform: rotate(45deg);
            transition: .3s;
            left: -37%;
        }

        &:after {
            content: url("../img/arrow_light.svg");
            position: absolute;
            vertical-align: top;
            margin-left: 9px;
            display: inline-block;
            transform: rotate(0);
            transition: .3s;
            position: absolute;
            right: 0;
        }
    }
}

.custom-newsletter {
    .wpcf7-form-control.wpcf7-text {
        margin-bottom: 20px;

        .mkdf-ms-explorer & {
            height: 54px;
        }

        @include ipad-landscape {
            max-width: 300px;
            margin: 0;
            vertical-align: top;
        }

        @include phone-landscape {
            margin-bottom: 20px;
        }
    }
}

.custom-newsletter-2 {

    .wpcf7-form-control.wpcf7-text {
        margin: 0 20px 0 0;
        display: inline-block;
        border-color: transparent;
        background-color: #ecebe1;
        max-width: 222px;

        .mkdf-ms-explorer & {
            height: 54px;
        }

        @include laptop-landscape-large {
            max-width:184px;
        }

        @include laptop-landscape-medium {
            max-width:154px;
        }

        @include ipad-landscape {
            margin-bottom: 20px;
            max-width:184px;
        }

        @include phone-landscape {
            max-width: 100%;
        }
    }

    .your-email {
        .wpcf7-form-control.wpcf7-text {
            margin-right: 19px;

            @include laptop-landscape-large {
                margin-right: 0;
            }
        }
    }
    
    .mkdf-btn.mkdf-btn-solid {
        overflow: hidden;
    
        .mkdf-btn-text {
            display: inline-block;
            padding-right: 20px;
            transition: .3s;
     
            &:after {
                content: url("../img/arrow_light.svg");
                position: absolute;
                vertical-align: top;
                margin-left: 9px;
                display: inline-block;
                transform-origin: center;
                transition: .3s;
            }

        }
        
        &:hover {
    
            .mkdf-btn-text {
                transform: translateX(11px);
                opacity: .1;
                transition: .3s .2s;
                 
                &:after {
                    margin-left: 60px;
                    transform: rotate(45deg);
                    transition: margin-left .3s .2s, transform .3s;
                }
            }
            
            &:before {
                transform: rotate(45deg);
                left: 37%;
                transition: .3s .2s;
            }

            &:after {
                transform: translateX(101%);
                transition: .3s .2s;
            }
        }
        
        &:before {
            content: url("../img/arrow_light.svg");
            vertical-align: top;
            margin-left: 9px;
            display: inline-block;
            position: absolute;
            transform: rotate(45deg);
            transition: .3s;
            left: -37%;
        }

        &:after {
            content: "";
            @include mkdfAbsoluteHolderLayout();
            background-color: #3c553a;
            transform: translateX(-102%);
            transform-origin: left;
            transition: .4s cubic-bezier(0.18, 0.24, 0.18, 0.89);
        }
    }

    .mkdf-btn.mkdf-btn-solid {
        vertical-align: top;

        @include laptop-landscape-large {
            margin-left: 20px;
            padding: 10px 30px;
        }

        @include ipad-landscape {
            margin-top: 0;
            margin-left: 20px;
        }

        @include phone-landscape {
            margin-left: 0;
        }
    }
}

.contact-us {
    .wpcf7-form-control.wpcf7-textarea,
    .wpcf7-form-control.wpcf7-text {
        border: none;
        background-color: #f6f6f6;
    }

    .wpcf7-form-control.wpcf7-text {
        width: calc(100% / 2 - 15px) ;

        .mkdf-ms-explorer & {
            height: 54px;
        }

        @include ipad-portrait {
            width: 100%;
        }
    }

    .your-name {
        margin-right: 30px;
    }

    .mkdf-btn.mkdf-btn-solid {
        margin-top: 10px;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.mkdf-slider-is-moving {
        .owl-item {
            .mkdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.mkdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                transform: translateY($pos);
            }
        }
    }

    &.mkdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $default-heading-color;
            background: none;
            border: 0;
            width: 65px;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;
            transform: translateY(-50%);
            transition: color .2s ease-out;

            &:hover {
                color: #fff;
    
                svg {
                    path,
                    polygon,
                    ellipse {
                        fill: #59815b;
                    }
                }
            }
            
            svg {
                position:absolute;
                top: 0;
                left: 0;
                z-index: -1;
    
                path,
                polygon,
                ellipse {
                    fill: #f6f6f6;
                    stroke: none;
	                transition: fill .2s ease-out;
                }
            }

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 46px;
                line-height: 1;

                .mkdf-ms-explorer & {
                    height: 40px;
                }

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: -30px;
        }

        .owl-next {
            right: -30px;
        }
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 6px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 12px;
                height: 12px;
                border-radius: 3em;
                background-color: transparent;
                border: 2px solid rgba($default-heading-color, .5);
                box-sizing: border-box;

                transition: background-color .2s ease-out, border .2s ease-out;
            }

            &:hover,
            &.active {

                span {
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .mkdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .mkdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                transition: border-color 0.2s ease-out;
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .mkdf-wrapper {
        padding: 0 !important;
    }

    .mkdf-top-bar {
        background-color: transparent;
    }

    .mkdf-content {
        height: 100vh;
        background-color: #aaa;
    }

    &.admin-bar {
        .mkdf-content {
            height: calc(100vh - 32px);
        }
    }

    .mkdf-content-inner {
        @include mkdfTableLayout();
    }

    .mkdf-page-not-found {
        @include mkdfTableCellLayout();
        padding: 0 20%;
        text-align: center;

        .mkdf-404-title {
            margin: 0;
            color: #fff;
        }

        .mkdf-404-subtitle {
            margin: 16px 0 0;
            color: #fff;
        }

        .mkdf-404-text {
            margin: 10px 0 0;
            padding: 0 33.5%;
            box-sizing: border-box;
            color: #fff;

            @include laptop-landscape-large {
                padding: 0 25%;
            }

            @include ipad-landscape {
                padding: 0 20%;
            }

            @include phone-landscape {
                padding: 0;
            }
        }

        .mkdf-btn {
            margin: 30px 0 0;

            &.mkdf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }

        .input-holder {
            @include mkdfRelativeHolderLayout();
            margin-top: 44px;

            input,
            button {
                height: $input-height;
                padding: 0;
                font-family: inherit;
                font-weight: inherit;
                line-height: 52px;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
            }

            input {
                @include mkdfRelativeHolderLayout();
                padding-left: 20px;
                padding-right: 20px;
                font-size: 18px;
                font-family: $additional-font;
                font-style: italic;
                color: #fff;
                background-color: transparent;
                border: 1px solid #fff;
                transition: border-color .2s ease-out;
                max-width:380px;
                margin-right: 18px;

                .mkdf-ms-explorer & {
                    height: 52px;
                }

                &:focus {
                    color: #fff;
                }

                @include mkdfPlaceholder {
                    color: #fff;
                }
            }

            button {
                @include mkdfButtonDefaultStyle();
                @include mkdfButtonSolidColor();
                @include mkdfButtonSize();

                @include phone-landscape {
                    margin-top: 20px;
                }
            }
        }
    }

    @include ipad-landscape {

        .mkdf-content {
            min-height: 100vh;
            margin-top: -70px!important;

            .mkdf-content-inner {
                min-height: 100vh;
            }
        }

        .mkdf-page-not-found {
            padding: 0 18% 76px;
        }
    }

    @include ipad-portrait {
        .mkdf-page-not-found {
            padding: 20px 30px 95px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        transition: opacity 0.15s ease, visibility .0s .15s;
    }

    &.on {
        opacity: 1;
        visibility: visible;

        transition: opacity 0.3s ease;
    }

    > span {
        position: relative;
        display: block;
        width: 45px;
        height: 45px;
        line-height: 40px;
        font-size: 30px;
        text-align: center;
        color: #fff;
        background-color: $first-main-color;
        overflow: hidden;
        transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;

        &:after {
            content: "";
            @include mkdfAbsoluteHolderLayout();
            width: 102%;
            background-color: #3c553a;            ;
            transform: translateY(100%);
            transform-origin: bottom;
            transition: .4s cubic-bezier(0.18, 0.24, 0.18, 0.89);
        }

        svg:nth-child(3) {
            position: absolute;
            top: 34%;
            left: 26%;
            height: 20px;
            width: 20px;
            fill: #fff;
            opacity: 0;
            transition: .2s;
        }

        svg:not(:nth-child(3)) {
            position: absolute;
            left: 35%;
            top: 32%;
            width: 15px;
            height: 15px;
            transition: .2s;
            z-index: 12;

            &:nth-child(2) {
                opacity: .3;
                transform: translateY(200%);
            }
            
            * {
                fill:none;
                stroke:#FFFFFF;
                stroke-width:2;
                stroke-miterlimit:10;
            }
        }

        &:hover {

            &:after {
                transform: translateY(-100%);
            }
    
            svg:last-child {
                opacity: 1;
                transition: .2s .2s;
            }

            svg {
                
                &:first-child {
                    transform: translateY(-200%);
                    transition: .2s .1s;
                }
    
                &:nth-child(2) {
                    transform: translateY(0);
                    transition: .2s .2s;
                }
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.mkdf-self-hosted-video-holder {
    .mkdf-video-wrap {
        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

a.mkdf-link-with-svg {
    position: relative;
    display: inline-block;
    padding: 0 29px;
    color: $default-heading-color;
    font-family: $default-heading-font;
    font-style: normal;
    font-size: 16px;
    font-weight: 600;
    line-height: 28px;
    text-transform: uppercase;
    
    .mkdf-active-hover {
        @include mkdfSVGbg(#ecebe1);
        clip-path: inset(0 100% 0 0);
        -webkit-clip-path: inset(0 100% 0 0);
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        position: absolute;
        margin-left: -18px;
        margin-top: 0;
        transition: .2s;
        will-change: transform;
        
        .mkdf-active-hover-middle {
            width: calc(100% - 34px);
            left: 12px;
        }
        
        svg.mkdf-active-hover-right {
            right: 17px;
        }

        svg.mkdf-active-hover-left {
            left: 0;
        }
        
        .mkdf-edge &, .mkdf-ms-explorer & {
            opacity: 0;
        }
    }
	
	.mkdf-link-with-svg-text {
		position: relative;
	}
	
    &:hover {
        color: $first-main-color;
        .mkdf-active-hover {
            clip-path: inset(0 0 0 0);
            -webkit-clip-path: inset(0 0 0 0);
            transition: 1s cubic-bezier(0.23, 0.35, 0, 1.09);
            
            .mkdf-edge &, .mkdf-ms-explorer & {
                opacity: 1;
                transition: .3s;
            }
        }
    }
}

.mkdf-comment-form {
    
    .mkdf-btn.mkdf-btn-solid:not(.mkdf-btn-custom-border-hover):hover {
        background-color: #4f6749 !important;
    }
}